<template>
	<v-form ref="editQform">
		<v-container>
			<v-layout row wrap>
				<v-flex xs12>
					<v-layout row wrap>
						<h4 class="display-1"> </h4>
					</v-layout>
				</v-flex>

				<v-flex xs12 mt-3>
					<v-label> </v-label>
					<v-tooltip bottom v-model="showTitleTooltip">
				        <v-btn slot="activator" @click="showTitleTooltip = !showTitleTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span>             .</span>
		        	</v-tooltip>
		            <v-text-field
		              v-model="question.title"
		              placeholder=", '   '"
		              required
              		  clearable
              		  :rules="[rules.title]"
              		  solo
		            ></v-text-field>
		        </v-flex>
		        <v-flex xs12>
		        	<v-label> </v-label>
		        	<v-tooltip bottom v-model="showTextTooltip">
				        <v-btn slot="activator" @click="showTextTooltip = !showTextTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span>       .</span>
		        	</v-tooltip>
			        <v-textarea
			            v-model="question.text"
			            placeholder=", '   2x - 5 = 14.'"
          		  		:rules="[rules.question]"
			            required
              			clearable
              			solo
			          ></v-textarea>
		        </v-flex>

		          <v-flex xs12 sm6 xl3>
		          	<v-layout align-center>
	        	  	  	<v-label> </v-label>
						<v-tooltip bottom v-model="showAttemptsTooltip">
					        <v-btn slot="activator" @click="showAttemptsTooltip = !showAttemptsTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
					        <span> .     ,   ,   .</span>
			        	</v-tooltip>
			        </v-layout>
	        		<v-layout align-center>
			          	<v-checkbox v-model="enabledAttempts" hide-details class="shrink mr-2"></v-checkbox>
			            <v-text-field 
				            type="number"
				            :disabled="!enabledAttempts"
	          		  		:rules="[rules.attempts]"
				            v-model="attempts"
	              			clearable
	              			solo
	              			persistent-hint
			            ></v-text-field>
				      </v-layout>
		          </v-flex>

		           <v-flex xs12 sm6 xl3>
    				<v-label>  </v-label>
    				<v-tooltip bottom v-model="showImageTooltip">
				        <v-btn slot="activator" @click="showImageTooltip = !showImageTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span> .      .</span>
		        	</v-tooltip>
        			<v-layout align-center>
			          	<v-checkbox v-model="enabledImage" hide-details class="shrink mr-2" ></v-checkbox>
			            <file-input 
			            class="fileBtn"
	                    accept="image/*"
	                    ref="fileInput"
                        @input="getUploadedQImage($event)"
                        @update:deleteFile="deleteFile(0)"
			            :dis="!enabledImage"
			            :checked="enabledImage"
			            :label="imageLoadText"
			            ></file-input>
			        </v-layout>
			      </v-flex>
<!--
		          <v-flex xs12 sm6 xl3>
        			<v-layout align-center>
			          	<v-checkbox v-model="enabledTimer" hide-details class="shrink mr-2"></v-checkbox>
			            <v-text-field type='number' 
			            :disabled="!enabledTimer"
			            :rules="rulesTimer"
			            label="  "
			            v-model="timer"
              			clearable
              			box
			            hint=" "
			            ></v-text-field>
			        </v-layout>
			      </v-flex>
-->
<!--
			      <v-flex xs12>
				      <v-label>   :</v-label>
				      <v-tooltip bottom v-model="showMediaTooltip">
					    <v-btn slot="activator" @click="showMediaTooltip = !showMediaTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
					    <span>        , ,     .     ,  ,     .        .</span>
			          </v-tooltip>
			      </v-flex>
-->
		         
				<v-flex xs12 v-for="mode in modes">
				  <div class="divider"></div>
				  <v-layout row wrap>
						<v-flex xs12 sm6>
							<p class="title">{{modes.indexOf(mode) + 1}}. : {{mode.name}}</p>
						</v-flex>
					</v-layout>

					<v-layout row wrap>
						<v-flex xs12 sm6 px-2>
			            <v-label>  </v-label>
			              <v-text-field
			                type="text"
			                v-model="videos[modes.indexOf(mode)]"
			                :rules="[rules.video]"
			                clearable
			                solo
			              ></v-text-field>
			            </v-flex>

			            <v-flex xs12 sm6 px-4>
			            <v-label> </v-label>
	        			<v-layout align-center>
				          	<v-checkbox @click.native="changeQAudio(modes.indexOf(mode))" v-model="enabledAudio[modes.indexOf(mode)]" hide-details class="shrink mr-2"></v-checkbox>
				            <file-input 
				            class="fileBtn"
		                    accept="audio/*"
		                    ref="fileInput"
	                        @input="getUploadedQAudio($event, modes.indexOf(mode))"
				            :dis="!enabledAudio[modes.indexOf(mode)]"
				            :checked="enabledAudio[modes.indexOf(mode)]"
				            :label="audioLoadText[modes.indexOf(mode)]"
				            ></file-input>
				        </v-layout>
				      </v-flex>
					</v-layout>

			  </v-flex>
				<div class="divider"></div>

				<v-flex xs10>
	              <v-slider
			        v-model="question.difficulty"
	                :max="maxDifficulty"
	                :min="minDifficulty"
	                label=" "
	              ></v-slider>
	            </v-flex>
	            <v-flex xs2>
	              <v-text-field
			        v-model="question.difficulty"
	                type="number"
	                :rules="[rules.difficulty]"
	                solo
	              ></v-text-field>
	            </v-flex>

				<v-flex xs12>
	    	  	  <v-label> </v-label>
	    	  	  <v-tooltip bottom v-model="showTypeTooltip">
				        <v-btn slot="activator" @click="showTypeTooltip = !showTypeTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span>     ,      .  " " ,      .  "  " ,        ,       .  "  " ,        ,        (   1    ,    ). </span>
		        	</v-tooltip>
		          <v-select
		            v-model="question.answer_type"
		            :items="answerTypes"
		            item-value="id"
		            item-text="val"
			        label=" "
		            solo
		            required
		           ></v-select>
		        </v-flex>

		        <v-flex xs12>
					<add-answers 
						:currentType="question.answer_type"
						:countAnswers="countAnswers"
						:answers="answers"
						:answersQty="answersQty"
						:edit="edit"
  						@update:countAnswers="countAnswers = $event"
  						@update:answersQty="answersQty = $event"
  						@push="pushAnswer()"
  						@update:answersAudio="getUploadedAudio($event)"
  						@update:answersImage="getUploadedImage($event)"
  						@update:changeAudio="changeAudio($event)"
  						@update:changeImage="changeImage($event)"
  						@update:clickHintTooltip="clickHintTooltip($event)"
  						@update:clickVideoTooltip="clickVideoTooltip($event)"
					></add-answers>
				</v-flex>
							    
				<v-flex xs12>
					<v-alert
			        :value="alert"
			        :type="successSet ? 'success' : 'error'"
			      	>
			        {{message}}

				    <v-btn v-if="successSet" to="/questions" flat>c  </v-btn>
				    <v-btn v-if="!successSet" @click.native="onSubmit()" flat>  </v-btn>
			      </v-alert>
		      </v-flex>	

		      <v-flex xs12>
					<v-btn round color="success" @click.native="onSubmit()" dark large>
						  
					</v-btn>
				</v-flex>

			</v-layout>
		</v-container>
	</v-form>
</template>

<script>
    import axios from 'axios'
	import router from '@/router'
    import FileInput from '@/components/other/FileLoader.vue'
    import AddAnswers from '@/components/boxes/AddAnswers.vue'
	import Authentication from '@/components/pages/Authentication'
	import connection from '@/router/connection'

	const TestingSystemAPI = connection.server

	export default {
        components: { FileInput, AddAnswers },

		data () {
		    return {
		    	enabledAttempts: false,
			    enabledTimer: false,
			    enabledImage: false,
			    showMediaTooltip: false,
			    showTitleTooltip: false,
			    showTextTooltip: false,
			    showAttemptsTooltip: false,
			    showImageTooltip: false,
			    showTypeTooltip: false,
			    edit:true,

				maxDifficulty: 100,
        		minDifficulty: 1,
        		maxAttempts: 100,
        		minAttempts: 1,
        		minTimer: 10,
        		maxTimer: 3600,
        		maxTitleLength: 100,
        		minTitleLength: 6,
        		maxQuestionLength: 2000,
        		minQuestionLength: 12,
        		maxVideoLength: 150,

      			selectedType: [],
      			answerTypes: [
	      			{ id: 1, val: ' '}, 
	      			{ id: 2, val : '  '}, 
	      			{ id: 3, val : '  '}
      			],

				rules: {
                	required: value => !!value || '  ',
                	title: str => (str.length >= this.minTitleLength && str.length <= this.maxTitleLength) || ' :  '+this.minTitleLength + '  ' + this.maxTitleLength + ' ',
                	question: str => (str.length >= this.minQuestionLength && str.length <= this.maxQuestionLength) || ' :  '+this.minQuestionLength + '  ' + this.maxQuestionLength + ' ',
                	difficulty: value => (value >= this.minDifficulty && value <= this.maxDifficulty) || '   '+this.minDifficulty+'  '+this.maxDifficulty,
                	attempts: value => (!this.enabledAttempts || value >= this.minAttempts && value <= this.maxAttempts) || '     '+this.minAttempts+'  '+this.maxAttempts,
                	timer: value => (!this.enabledTimer || value >= this.minTimer && value <= this.maxTimer) || '     '+this.minTimer+'  '+this.maxTimer,
                	video: str => (str.length <= this.maxVideoLength) || '    '+this.maxVideoLength + ' ',
                },
        		
				message: '',
				alert: false,
				successSet: false,
				getQError: false,
				attempts: null,
				timer: null,
				answersQty: null,
				enabledAudio:[],
				videos:[],
				audios:[],
				old_audios:[],
				audioLoadText:[],
				qMediaData:[],
				hints: [],
				modes:[],

				questionId: null,
				answers: [],
				question: null,
				image: null,

                imageLoadText: '  ',
		    }
		  },
		methods: {
			getQuestion() {
				axios.get(`${TestingSystemAPI}/api/questions/`, {
		            headers: { 'Authorization': Authentication.getAuthenticationHeader(this) },
		            params: { 'id' : this.$route.params.id }
		        }).then((qdata) => {
					this.question = qdata.data[0]
					this.enabledAttempts = !!this.question.attempts_number
					this.enabledTimer = !!this.question.timer
					this.enabledImage = false
       				this.answersQty = this.question.answers_number
					if (this.question.image)
						this.imageLoadText = '   '
					this.question.old_image = this.question.image
					this.question.image = null
					this.attempts = this.question.attempts_number
					this.timer = this.question.timer

					axios.get(`${TestingSystemAPI}/api/questions_media/`, {
			            headers: { 'Authorization': Authentication.getAuthenticationHeader(this) },
			            params: { 'id' : this.$route.params.id }
			        }).then((response) => {
			        	this.qMediaData = response.data
			        	console.log('qMediaData')
			        	console.log(this.qMediaData)

			        	for (let k = 0; k < this.qMediaData.length; k++)
			        	{
			        		this.enabledAudio.push(false)
			        		this.audios.push(null)
			        		this.videos.push('')
			        		if (this.qMediaData[k].audio && this.qMediaData[k].audio.search("null") != -1)
				    			this.qMediaData[k].audio = null
			        		if (!!this.qMediaData[k].audio)
			        			this.audioLoadText.push('   ')
			        		else this.audioLoadText.push('  ')
			        		this.old_audios.push(this.qMediaData[k].audio)
			        	}

			        	axios.get(`${TestingSystemAPI}/api/answers/`, {
				            headers: { 'Authorization': Authentication.getAuthenticationHeader(this) },
				            params: { 'id' : this.$route.params.id }
				        }).then((adata) => {
							this.answers = adata.data
							for (var i = 0; i < this.answers.length; i++)
							{

								this.answers[i].enabledImage = false
								if (this.answers[i].image)
									this.answers[i].imageLoadText = '   '
								else this.answers[i].imageLoadText = '   '
								this.answers[i].showMediaTooltip = false
								this.answers[i].old_image = this.answers[i].image
								this.answers[i].image = null

	               				axios.get(`${TestingSystemAPI}/api/answers_hint/`, {
						            headers: { 'Authorization': Authentication.getAuthenticationHeader(this) },
						            params: { 'id' : this.answers[i].id }
						        }).then((response) => {
						        	console.log('answers_hint'+(i+1))
						        	console.log(this.response.data)
						        	this.hints.push(response.data)
					        	}).catch(error => {
				                    this.alert = true
				                    this.message = '     .    .'
				                })

							}

				        }).catch(error => {
		                    this.alert = true
		                    this.message = '     .    .'
		                })
		        	})
	               .catch(error => {
	               		this.successSet = false
	                    this.alert = true
	                    this.message = '   .    .'
	                })

					
		        }).catch(error => {
                    this.alert = true
                    this.message = '     .    .'
                })
			},
            getUploadedQImage(e) {
                this.question.image = e
            },
            getUploadedQAudio(e, mode) {
                this.audios[mode] = e
            },
            changeQAudio(mode){
            	if (!this.enabledAudio[mode])
       				this.audios[mode] = null
            },
            getUploadedAudio(obj) {
    			this.answers[obj.index].audio = obj.audio
    			this.answers.push(null)
    			this.answers.pop()
            },
            getUploadedImage(obj) {
    			this.answers[obj.index].image = obj.image
    			this.answers.push(null)
    			this.answers.pop()
            },
            changeAudio(i){
				if (!this.answers[i].enabledAudio){
					this.answers[i].audio = null
        			if (this.answers[i].old_audio)
						this.answers[i].audioLoadText = "   "
					else this.answers[i].audioLoadText = "   "
				}
				else this.answers[i].audioLoadText = "   "
				this.answers.push(null)
				this.answers.pop()
            },
            changeImage(i){
				if (!this.answers[i].enabledImage){
					this.answers[i].image = null
        			if (this.answers[i].old_image)
						this.answers[i].imageLoadText = "   "
					else this.answers[i].imageLoadText = "   "
				}
				else this.answers[i].imageLoadText = "   "
				this.answers.push(null)
				this.answers.pop()
            },
            onSubmit() {
	        	if (!this.$refs.editQform.validate())
	        	{
               		this.successSet = false
                    this.alert = true
                    this.message = '     .'
	        		return
	        	}
	        	if (this.successSet)
	        		return

                 let formData = new FormData()

                 formData.set('text', this.question.text)
                 formData.set('title', this.question.title)
                 formData.set('attempts_number', this.attempts)
                 formData.set('timer', this.timer)
                 formData.set('answer_type', this.question.answer_type)
                 formData.set('answers_number', this.answersQty)
                 formData.set('difficulty', this.question.difficulty)

                 if (this.question.old_image && !this.enabledImage)
                 	formData.set('image', "stay")
                 else if (this.question.old_image && !this.question.image)
                 	formData.set('image', null)
                 else formData.set('image', this.question.image)

                 if (this.question.old_audio && !this.enabledAudio)
                 	formData.set('audio', "stay")
                 else if (this.question.old_audio && !this.question.audio)
                 	formData.set('audio', null)
                 else formData.set('audio', this.question.audio)

                 var comment = null
                 if (this.currentType == 1)
					comment = this.answers[0].comment
                 formData.set('comment', comment)
				/*
				var object = {};
				formData.forEach(function(value, key){
				    object[key] = value;
				});
				var json = JSON.stringify(object);
                 console.log(json)
				*/
                 axios.patch(TestingSystemAPI+'/api/questions/'+this.$route.params.id+'/', formData, {
			          headers: { 'Authorization': Authentication.getAuthenticationHeader(this) },
			          params: {}
			        })
	               .then((response) => {
	               		this.questionId = response.data.id
	               		for (var i = 0; i < this.answers.length; ++i)
	               			this.answers[i].question = this.questionId

                 		let answersData = new FormData()

		                for (var i = 0; i < this.answers.length; i++)
		                {
			                if (this.answers[i].old_image && !this.answers[i].enabledImage)
			                 	answersData.set('image', "stay")
			                else if (this.answers[i].old_image && !this.answers[i].image)
			                 	answersData.set('image', null)
			                else answersData.set('image', this.answers[i].image)

			                if (this.answers[i].old_audio && !this.answers[i].enabledAudio)
			                 	answersData.set('audio', "stay")
			                else if (this.answers[i].audio && !this.answers[i].audio)
			                 	answersData.set('audio', null)
			                else answersData.set('audio', this.answers[i].audio)

		                 	if (this.currentType != 2 )
		                 		answersData.append('priority', this.answers[i].priority)
		                 	else 
		                 	{
		                 		if (this.answers[i].correct)
		                 			answersData.append('priority', this.answers.length)
		                 		else answersData.append('priority', i + 1)
		                 	}
		                 	answersData.append('text', this.answers[i].text)
		                 	answersData.append('correct', this.answers[i].correct)
		                 	if (this.currentType == 2 && !this.answers[i].correct)
		                 		answersData.append('weight', 0)
		                 	else answersData.append('weight', this.answers[i].weight)
		                 	answersData.append('hint', this.answers[i].hint)
		                 	answersData.append('question', this.questionId)
		                 	/*
		                 	var object = {};
							answersData.forEach(function(value, key){
							    object[key] = value;
							});
							var json = JSON.stringify(object);
			                 console.log(json)
			                 */
		                 }

               			axios.patch(TestingSystemAPI+'/api/answers/'+this.$route.params.id+'/', answersData, {
				          headers: { 'Authorization': Authentication.getAuthenticationHeader(this) },
				          params: { }
				        })
		               .then(response => {
		               		this.successSet = true
		                    this.alert = true
		                    this.message = '  .'
	                    	setTimeout(this.goBack, 1200)
		                })
		               .catch(error => {
		                    this.alert = true
		                    this.message = '   .    .'
		                })
	                })
	               .catch(error => {
	                    this.alert = true
	                    this.message = '   .    .'
	                })
            },
			reloadPage() {
				window.location.reload(true)
			},
			goBack(){
            	router.push('/tests')
			},				
			deleteFile(isAudio){
				if (isAudio)
					this.question.audio = null
				else this.question.image = null
			},
			pushAnswer(ind=null) {
				if (!ind)
				{
					var isTrue = this.currentType == 1 ? true : false
					this.answers.push({
	                	image: null,
	                	text: null,
	                	priority: this.answers.length+1,
	                	correct: isTrue,
	                	weight: 256,
	                	comment: null,
	                	question: 0,
						enabledImage: false,
						enabledAudio:[],
						audioLoadText:[],
						showHintTooltip:[],
						showVideoTooltip:[],
						audios:[],
						hints:[],
						videos:[],
						old_audios:[]
					})

					for (var i = 0; i < this.qMediaData.length; i++){
						this.answers[this.answers.length - 1].audios.push(null)
						this.answers[this.answers.length - 1].old_audios.push(null)
						this.answers[this.answers.length - 1].enabledAudio.push(false)
						this.answers[this.answers.length - 1].showHintTooltip.push(false)
						this.answers[this.answers.length - 1].showVideoTooltip.push(false)
						this.answers[this.answers.length - 1].videos.push('')
						this.answers[this.answers.length - 1].audioLoadText.push('  ')
						this.answers[this.answers.length - 1].hints.push('')
					}
				}
				else
				{
					this.answers.push({
	                	image: this.answers[ind].image,
	                	text: this.answers[ind].text,
	                	priority: this.answers[ind].priority,
	                	correct: this.answers[ind].correct,
	                	weight: this.answers[ind].weight,
	                	comment: this.answers[ind].comment,
	                	question: this.$route.params.id,
						enabledImage: true,
						enabledAudio:[],
						audioLoadText:[],
						showHintTooltip:[],
						showVideoTooltip:[],
						audios:[],
						hints:[],
						videos:[],
						old_audios:[]
					})

					for (var i = 0; i < this.qMediaData.length; i++){
						this.answers[this.answers.length - 1].audios.push(null)
						this.answers[this.answers.length - 1].old_audios.push(this.hints[ind][i].audio)
						this.answers[this.answers.length - 1].enabledAudio.push(false)
						this.answers[this.answers.length - 1].showHintTooltip.push(false)
						this.answers[this.answers.length - 1].showVideoTooltip.push(false)
						this.answers[this.answers.length - 1].videos.push(this.hints[ind][i].video)
						this.answers[this.answers.length - 1].audioLoadText.push(!!this.hints[ind][i].audio ? '   ' : '  ')
						this.answers[this.answers.length - 1].hints.push(this.hints[ind][i].text)
					}
				}

			},
			getModeData(){				
				axios.get(`${TestingSystemAPI}/api/strict_modes/`, {
		          headers: { 'Authorization': Authentication.getAuthenticationHeader(this) },
		          params: {}
		        }).then(({data}) => {
		          this.modes = data

       			  //this.pushAnswer()

		        }).catch(error => {
		          this.alert = true
		          this.alert_message = '    .    .'
		        })
			}
       },
       watch: {
       		currentType: function(val){
       			if (val == 1)
       				this.countAnswers = 1
       			else if (val == 2 || val == 3)
       				this.countAnswers = 2

       			this.answers.splice(0)
       			while (this.answers.length < this.countAnswers)
       					this.pushAnswer()
       		},
       		enabledImage: function(val) {
       			if (!val)
       			{
       				this.question.image = null
       				if (this.question.old_image)
       					this.imageLoadText = '   '
       				else this.imageLoadText = '  ' 
       			}
       			else 
       				this.imageLoadText = '  ' 

       		},
       		enabledAudio: function(val) {
       			if (!val)
       			{
       				this.question.audio = null
       				if (this.question.old_audio)
       					this.audioLoadText = '   '
       				else this.audioLoadText = '  '
       			}
       			else 
       				this.audioLoadText = '  '
       		},
       		attempts: function(val) {
   				if (this.enabledAttempts)
       				this.attempts = val
       			else this.attempts = null
       		},
       		timer: function(val) {
   				if (this.enabledTimer)
       				this.timer = val
       			else this.timer = null
       		}
       },
       computed: {
       		countAnswers: function(val) {
       			return this.answers.length
       		},
       },
       mounted() {
   		this.getModeData()
   		this.getQuestion()
   		for (var i = 0; i < this.answers.length; i++)
   			this.pushAnswer(i)
       }
	}
</script>

